.container {
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;


    @media screen and (min-width: 320px) {
    max-width: 320px;
          padding: 0;
    margin: 0 auto;
  }

  @media screen and (min-width: 480px) {
    max-width: 480px;
          padding: 0;
    margin: 0 auto;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
          padding: 0;
    margin: 0 auto;
  }

  @media screen and (min-width: 1200px) {
    max-width: 1200px;
      padding: 0;
    margin: 0 auto;
  }

}